import { Component, OnInit } from '@angular/core';
import { AuthService } from "../auth.service";
import { FormBuilder, FormGroup, Validators, FormArray } from "@angular/forms";
import { userInfo } from 'os';
import { element } from 'protractor';
import { Router } from "@angular/router";
import { idbService } from "../service/idb.service";
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as moment from "moment";

@Component({
  selector: 'app-pin-generate',
  templateUrl: './pin-generate.component.html',
  styleUrls: ['./pin-generate.component.css']
})
export class PinGenerateComponent implements OnInit {
  public PinForm: FormGroup;
  alert_msg = "";
  showHead: true;
  showForgot: boolean;
  disabledRP: boolean;
  CompanyId: number;
  StoreId: number;
  store: any;
  key = 'Name';
  UserStores: any;
  Show = false;
  data: any;
  pin: any;
  newShift: boolean = false;
  openBal: number;
  currShift: any;
  constructor(private Auth: AuthService, private _fb: FormBuilder, private router: Router,
    private IDB: idbService, private modalService: NgbModal) {
    var logInfo = JSON.parse(localStorage.getItem("logInfo"));
    this.CompanyId = logInfo.CompanyId;
    this.StoreId = logInfo.StoreId;
    this.currShift = JSON.parse(localStorage.getItem("shift"));
  }

  ngOnInit() {
    this.showForgot = false;
    this.disabledRP = false;
    showHead: false;
  }
  unlock() {
    var storeUsers = JSON.parse(localStorage.getItem("storeUsers"));
    // console.log(storeUsers);
    for(let i = 0; i < storeUsers.length; i++)
    {
      var element = storeUsers[i]
      if (element.Pin == this.pin) {
        // console.log("crrct")
        var currShift = JSON.parse(localStorage.getItem("shift"));
        var enforceShift = false;
        if (currShift != undefined && enforceShift) {
          if (currShift.UserId == element.UserId) {
            // localStorage.setItem("lockStatus","unlocked");
            this.router.navigate(["/order"]);
          }
          else {
            this.alert_msg = "Previous User Must close the session";
            break;
          }
        }
        else {
          if (this.newShift) {
            var user = JSON.stringify({ Id: element.UserId, Name: element.Name, Role: element.Role, RoleId: element.RoleId });
            var shift = {
              ShiftStartTime: moment().format("YYYY-MM-DD HH:MM"),
              UserId: element.UserId,
              SalesDate: moment().format("YYYY-MM-DD"),
              OpeningBalance: this.openBal,
              ClosingBalance: 0,
              StoreId: this.StoreId,
              CompanyId: this.CompanyId,
              Id: 0
            }
            this.Auth.AddShift({ data: JSON.stringify(shift) },moment().format("YYYY-MM-DD HH:mm:ss")).subscribe(data => {
              localStorage.setItem("shift", JSON.stringify(data));
              localStorage.setItem("user", user);
              // localStorage.setItem("lockStatus","unlocked");
              this.router.navigate(["/order"]);
            });
          }
          else {
            var user = JSON.stringify({ Id: element.UserId, Name: element.Name, Role: element.Role, RoleId: element.RoleId });
            localStorage.setItem("user", user);
            this.router.navigate(["/order"]);
            // this.alert_msg = "please open shift"
            break;
          }
        }
        break;
      }
      else {
        // console.log("error");
        this.alert_msg = "Invalid Pin"
      }
    }
  }
}