import { Injectable } from '@angular/core';
import { HubConnection, HubConnectionBuilder } from '@aspnet/signalr';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class SignalRService {

  hubconnection: HubConnection = new HubConnectionBuilder().withUrl('https://biz1pos.azurewebsites.net/chatHub').build();
  CompanyId;
  StoreId;
  hubRoom;
  isconnected: boolean = false;
  logInfo;
  shouldConnect: boolean = true;
  constructor(public router: Router) {
    this.logInfo = JSON.parse(localStorage.getItem("logInfo"));
    if (this.logInfo != (undefined || null)) {
      this.CompanyId = this.logInfo.CompanyId;
      this.StoreId = this.logInfo.StoreId;
      this.hubRoom = this.StoreId + '/' + this.CompanyId;
    }

    this.hubconnection.onclose((err) => {
      console.log('disconnected!!!!!')
      console.log(err)
      this.isconnected = false;
      this.connect();
    });

    this.hubconnection.on("joinmessage", data => {
      this.hubconnection.invoke('GetStoreOrders', this.hubRoom, this.StoreId);
    })
  }

  setLogInfo() {
    this.logInfo = JSON.parse(localStorage.getItem("logInfo"));
    console.log(this.logInfo)
    if (this.logInfo != (undefined || null)) {
      this.CompanyId = this.logInfo.CompanyId;
      this.StoreId = this.logInfo.StoreId;
      this.hubRoom = this.StoreId + '/' + this.CompanyId;
    }
  }

  connect() {
    if (navigator.onLine && this.logInfo != (undefined || null)) {
      this.hubconnection
        .start()
        .then(() => {
          console.log('Connection started!');
          this.isconnected = true;
          this.hubconnection.invoke('JoinRoom', this.hubRoom, this.StoreId);
        })
        .catch((err) => {
          setTimeout(() => this.connect(), 2000);
        })
    } else {
      setTimeout(() => {
        this.setLogInfo();
        this.connect();
      }, 2000);
    }
  }

  joinroom() {
    if (this.isconnected) {
      this.hubconnection.invoke('JoinRoom', this.hubRoom, this.StoreId);
    } else {
      setTimeout(() => this.joinroom(), 2000);
    }
  }

  getOrders() {
    if (this.isconnected) {
      this.hubconnection.invoke('GetStoreOrders', this.hubRoom, this.StoreId);
    } else {
      setTimeout(() => this.getOrders(), 2000);
    }
  }

  disconnect() {
    this.logInfo = null;
    this.CompanyId = null;
    this.StoreId = null;
    this.hubRoom = null;
    this.shouldConnect = false;
    this.hubconnection
      .stop()
      .then(() => {
        console.log("Terminated Succesfully :)")
      })
      .catch(() => {
        this.shouldConnect = true;
        console.log("Error Terminating :o")
      })
  }
}
