import { Component, Injectable, Input, Output, EventEmitter } from '@angular/core';
import { Observable, fromEvent, merge, of } from 'rxjs';
import { mapTo } from 'rxjs/operators';
import { idbService } from './idb.service';
import { AuthService } from '../auth.service';
import { element } from 'protractor';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  @Output() fire: EventEmitter<any> = new EventEmitter();
  @Output() order: EventEmitter<any> = new EventEmitter();
  @Output() notification: EventEmitter<any> = new EventEmitter();
  updateEveryMS = 1000;
  online$: boolean;
  orderList: any;
  unsyncItems: any;
  unsynced: number;
  tempdata: any;
  constructor(private IDB: idbService, private Auth: AuthService) {

  }

  ngOnInit() {

  }
  ngAfterViewInit() {
    // this.execute();
  }
  getlocaldata() {
    return localStorage.getItem("OrderNo")
  }
  sync() {
    this.syncNowOrder();
    this.syncPreOrder();
    this.saveprintersettings();
  }
  saveprintersettings() {
    var bool = false
    if (navigator.onLine) {
      var storeId = JSON.parse(localStorage.getItem("logInfo")).StoreId;
      var store;
      this.Auth.editStore(storeId).subscribe(data => {
        store = data;
        // console.log(store);
        var printersettings = JSON.parse(localStorage.getItem("printersettings"))
        store.receiptprinter = printersettings.receiptprinter;
        store.kotprinter = printersettings.kotprinter;
        this.Auth.updateStore({ data: JSON.stringify(store) }).subscribe(response => {
          // console.log(response)
          bool = true;
         });
      });
    }
  }
  syncNowOrder() {
    var receipts = JSON.parse(localStorage.getItem("pendingReceipts"));
    var pendingrcpts: any = Object.values(receipts)[0];
    // console.log(pendingrcpts, "sync")
    if (pendingrcpts && navigator.onLine) {
      this.Auth.saveOrder({ ordData: JSON.stringify([pendingrcpts]) }).subscribe(data => {
        var response: any = data;
        // if (pendingrcpts.OrderTypeId != 1) {
        //   var index = preOrders.findIndex(x => x.Id == pendingrcpts.Id);
        //   preOrders[index]["OrderId"] = response.OrderId
        //   localStorage.setItem("preOrders", JSON.stringify(preOrders));
        //   this.emitOrder();
        // }
        if (response.status == 200) {
          delete receipts[pendingrcpts.Id.toString()];
          localStorage.setItem("pendingReceipts", JSON.stringify(receipts));
        }
        setTimeout(() => {
          this.syncNowOrder();
        }, 4000);
      });
    }
  }
  syncPreOrder() {
    var preOrders = JSON.parse(localStorage.getItem("preOrders"));
    if (preOrders && navigator.onLine) {
      preOrders.forEach(element => {
        if (element.islive == 0) {
          element.issync = 1;
          element.islive = 1;
          this.Auth.saveOrder({ ordData: JSON.stringify([element]) }).subscribe(data => {
            var response: any = data;
            if (response.status == 200) {
              element.OrderId = response.OrderId;
              localStorage.setItem("preOrders", JSON.stringify(preOrders));
              this.emitOrder();
            } else if (response.status == 0) {
              element.issync = 0;
              element.islive = 0;
              localStorage.setItem("preOrders", JSON.stringify(preOrders));
            }
          });
        } else if (element.issync == 0) {
          element.issync = 1;
          element.islive = 1;
          this.Auth.editOrder({ ordData: JSON.stringify([element]) }).subscribe(data => {
            var response: any = data;
            if (response.status == 200) {
              if (element.PaidAmount > element.BillAmount) {
                element.PaidAmount = element.BillAmount;
              }
              localStorage.setItem("preOrders", JSON.stringify(preOrders));
              this.emitOrder();
            } else if (response.status == 0) {
              element.issync = 0;
              localStorage.setItem("preOrders", JSON.stringify(preOrders));
            }
          });
        }
      });
    }
  }
  async execute() {
    while (false) {
      this.online$ = navigator.onLine;
      // console.log("'execute'");
      if (this.online$) {
        var orders;
        var usorder;
        this.IDB.IDBGetStoreObser("Orders").subscribe(data => {
          orders = data;
          usorder = orders.filter(x => x.islive == 0 || x.issync == 0)[0];
          // orders.forEach(element => {
          if (usorder.islive == 0) {
            var postdata = { ordData: JSON.stringify([usorder]) };
            usorder.islive = 1;
            usorder.issync = 1;
            this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => {
              this.Auth.saveOrder(postdata).subscribe(x => {
                var response: any = x;
                if (response.status == 200) {
                  usorder.OrderId = response.OrderId;
                  usorder.islive = 1;
                  usorder.issync = 1;
                  this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => { });
                  this.emitOrder();
                } else {
                  usorder.islive = 0;
                  usorder.issync = 0;
                  this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => { });
                  this.emitOrder();
                }
              })
            });
          } else if (usorder.issync == 0) {
            var postdata = { ordData: JSON.stringify([usorder]) };
            usorder.issync = 1;
            this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => {
              this.Auth.editOrder(postdata).subscribe(data => {
                var response: any = data;
                if (response.status == 200) {
                  usorder.islive = 1;
                  usorder.issync = 1;
                  if (usorder.PaidAmount > usorder.BillAmount) {
                    usorder.PaidAmount = usorder.BillAmount;
                  }
                  this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => {
                    this.emitOrder();
                  });
                } else {
                  usorder.issync = 0;
                  this.IDB.IDBUpdateStore("Orders", usorder).subscribe(z => {
                    this.emitOrder();
                  });
                }
              });
            });
          } else {
            if (usorder.OrderTypeId == 1 && usorder.islive == 1 && usorder.issync == 1) {
              this.IDB.IDBDeleteStoreByKey("Orders", usorder.Id).subscribe(deldat => {

              });
            }
          }
          // });
        });
      }
      else {
      }
      await this.sleep(30000);
    }
  }
  sleep(ms) {
    // ret urn;
    return new Promise(resolve => setTimeout(resolve, ms));
  }
  update(no) {
    this.fire.emit(no);
  }
  stopnotification_req() {
    this.notification.emit('stopnotification');
  }
  stopnotification() {
    return this.notification;
  }
  getupdated() {
    return this.fire;
  }

  emitOrder() {
    this.order.emit("ordersynced");
  }

  refreshOrder() {
    return this.order;
  }
}