  aggregatorOrdersConfig() {
    try {
      this.signalR.hubconnection.on("order", (order) => {
        console.log('order', order);
        var orders = order;
        orders.forEach((element, index) => {
          if (typeof (element.json) == 'string') {
            element.json = JSON.parse(element.json);
          }

          if (element.riderDetails != null && typeof (element.riderDetails) == 'string') {
            element.riderDetails = JSON.parse(element.riderDetails);
          }
        });
        this.AggregatorOrders = orders;
      });
    } catch (error) {
      console.log(error)
    }
  }

  socketlistenconfig() {
    try {
      this.socket.on('hold/release-table', data => {
        var table = JSON.parse(localStorage.getItem('DiningTable'));
        var tableitem = this.tables.filter(x => x.Id == data.id)[0]
        this.createWaiterOrder(1, tableitem);
        // localStorage.setItem('DiningTable', JSON.stringify(table));
        // this.getTables();
      });

      this.socket.on('placingorder', data => {
        console.log(data);
        var table = this.tables.filter(x => x.Id == data.TableId)[0];
        let splitId = table.lastspliteid == undefined ? 0 : table.lastspliteid;
        var TableKey = "TableOrder_" + table.Id + "_" + splitId;
        this.createWaiterOrder(1, table);
        data.items.forEach(element => {
          this.waiterorder.AddItem(element, element.Quantity);
        });
        this.waiterorderkot(TableKey)
      });
    }
    catch (error) {
      console.log(error)
    }

  }
  createWaiterOrder(type, item) {
    this.clearWaiterOrder();
    if (type == 1) {
      item.TableStatusId = 1;
      this.waiterorder.DiningTableId = item.Id;
      let splitId = item.lastspliteid == undefined ? 0 : item.lastspliteid;
      this.waiterorder.SplitTableId = item.lastspliteid;
      var TableKey = "TableOrder_" + item.Id + "_" + splitId;
      var TableOrder = this.LOCS.getDataall(TableKey);
      if (TableOrder == null) {
        this.LOCS.setData(TableKey, this.waiterorder);

        this.waiterorder.OrderTypeId = type;
      } else {
        for (var k in TableOrder) this.waiterorder[k] = TableOrder[k];
      }
      // if (item.lastspliteid > 0) {
      //   this.currentSpliteItem = item;
      // } else {
      //   this.currentSpliteItem = null;
      // }
      this.getTables();
    }
    // if (this.currentSpliteItem != null) {
    //   this.orderName = this.currentTable + '/' + this.currentTableKey.slice(13, 14);
    // }
    this.updatedNotification();
  }
  waiterorderkot(tablekey) {
    var checkqty;
    var tempitems = [];
    var tempitobj;
    this.waiterorder.item.forEach(item => {
      tempitobj = Object.assign({}, item)
      if (this.waiterorder.KOT.length == 0) {
        tempitobj.StatusId = 1;
        tempitems.push(tempitobj);
      } else {
        checkqty = 0;
        var kotqty = 0
        this.waiterorder.KOT.forEach(kot => {
          kot.item.forEach(kitem => {
            if (item.Key == kitem.Key) {
              kotqty = kotqty + kitem.Quantity;
            }
          });
        });
        checkqty = item.Quantity - kotqty;
        if (checkqty > 0) {
          tempitobj.Quantity = checkqty;
          tempitobj.StatusId = 1;
          tempitems.push(tempitobj);
        } else if (checkqty < 0) {
          tempitobj.Quantity = checkqty;
          tempitobj.StatusId = -1;
          tempitems.push(tempitobj);
        }
      }
    });
    this.item.forEach(element => {
      tempitems.push(element);
    });
    this.item = [];
    tempitems.sort((item1, item2) => {
      if (item1.KOTGroupId < item2.KOTGroupId) return -1;
      if (item1.KOTGroupId > item2.KOTGroupId) return 1;
    });
    var obj = [];
    let checkkot = 0
    tempitems.forEach((tmit, index, array) => {
      tmit.KOTNo = +localStorage.getItem("KOTNo");
      if (index == 0) {
        obj.push(tmit);
      } else if (tmit.KOTGroupId == checkkot) {
        obj.push(tmit);
      } else {
        this.waiterorder.addKOT(obj)
        obj = [];
        localStorage.setItem("KOTNo", tmit.KOTNo + 1);
        tmit.KOTNo = +localStorage.getItem("KOTNo");
        obj.push(tmit);
      }
      if (index == array.length - 1) {
        this.waiterorder.addKOT(obj)
        obj = [];
        localStorage.setItem("KOTNo", tmit.KOTNo + 1);
        tmit.KOTNo = +localStorage.getItem("KOTNo");
        obj.push(tmit);
      }
      checkkot = tmit.KOTGroupId;
    })
    this.LOCS.setData(tablekey, this.waiterorder);
  }
