  mapVarient(products, group, varient) {
    products.forEach(elementProduct => {
      if (elementProduct.hasOwnProperty("VariantGroup")) {
        elementProduct.VariantGroup.forEach(varientGroup => {
          varientGroup.varient = [];
          group.forEach(elementGroup => {
            let Group = [];
            if (varientGroup.Id == elementGroup.Id) {
              varientGroup.name = elementGroup.VariantGroup;
              varientGroup.selected = 0;
              if (varientGroup.Id == elementGroup.Id) {
                varient.forEach(elementVarient => {
                  if (
                    elementProduct.Id == elementVarient.ProductId &&
                    elementGroup.Id == elementVarient.VariantGroupId
                  ) {
                    elementVarient.isselect = 0;
                    Group.push(elementVarient);
                  }
                });
              }
              if (Group != undefined) {
                if (Group.length > 0) {
                  varientGroup.varient = Group;
                }
              }
            }
          });
        });
      }
    });
  }

  // getAddonGroup(products) {
  //   const vrntObservable = this.IDB.IDBGetStoreObser("AddonGroup");
  //   vrntObservable.subscribe(Data => {
  //     let AddonGroup = Data;
