  mapAddon(products, group, varient) {
    let groupArray = [];
    let addonArray = [];
    group.forEach(element => {
      groupArray[element.Id] = element;
    });
    varient.forEach(element => {
      addonArray[element.Id] = element;
    });
    products.forEach(elementProduct => {
      if (elementProduct.hasOwnProperty("AddonGroup")) {
        elementProduct.AddonGroup.forEach(AddonGroup => {
          AddonGroup.addon = [];
          AddonGroup.selected = 0;
          let AddonGroupKey = AddonGroup.Id;

          if (groupArray.hasOwnProperty(AddonGroupKey)) {
            AddonGroup.name = groupArray[AddonGroupKey].AddonGroup;

            if (AddonGroup.hasOwnProperty("Addon")) {
              AddonGroup.Addon.forEach(Addon => {
                Addon.selected = 0;
                let AddonKey = Addon.Id;
                if (addonArray.hasOwnProperty(AddonKey)) {
                  Addon.name = addonArray[AddonKey].Description;
                }
              });
            }
          }
        });
      }
    });
  }
  setCurrentProduct(Item) {
    this.getProduct();
    this.CurrentProduct = [];
    this.product.forEach(elementProduct => {
      if (elementProduct.Id == Item.ProductId) {
        this.CurrentProduct = elementProduct;
        let AddonArray = [];
        let VariantArray = [];
        let OptionArray = [];
        if (Item.hasOwnProperty("AddonGroup")) {
          Item.AddonGroup.forEach(ItemAddonGroup => {
            ItemAddonGroup.Addon.forEach(element => {
              AddonArray[element.Id] = element.Id;
            });
          });
        }
        if (Item.hasOwnProperty("VariantGroup")) {
          Item.VariantGroup.forEach(ItemVarientGroup => {
            ItemVarientGroup.Variant.forEach(element => {
              VariantArray[element.Id] = element.Id;
            });
          });
        }
        if (Item.hasOwnProperty("OptionGroup")) {
          Item.OptionGroup.forEach(ItemOptionGroup => {
            ItemOptionGroup.Option.forEach(element => {
              OptionArray[element.Id] = element.Id;
            });
          });
        }

        if (this.CurrentProduct.hasOwnProperty("AddonGroup")) {
          this.CurrentProduct.AddonGroup.forEach(AddonGroup => {
            if (AddonGroup.hasOwnProperty("Addon")) {
              AddonGroup.Addon.forEach(Addon => {
                let AddonKey = Addon.Id;
                if (AddonArray.hasOwnProperty(AddonKey)) {
                  Addon.selected = AddonKey;
                  AddonGroup.selected = AddonKey;
                }
              });
            }
          });
        }

        if (this.CurrentProduct.hasOwnProperty("VariantGroup")) {
          this.CurrentProduct.VariantGroup.forEach(VariantGroup => {
            if (VariantGroup.hasOwnProperty("varient")) {
              VariantGroup.varient.forEach(varient => {
                let varientKey = varient.Id;
                if (VariantArray.hasOwnProperty(varientKey)) {
                  varient.selected = 1;
                }
              });
            }
          });
        }

        if (this.CurrentProduct.hasOwnProperty("OptionGroup")) {
          this.CurrentProduct.OptionGroup.forEach(OptionGroup => {
            if (OptionGroup.hasOwnProperty("Option")) {
              OptionGroup.Option.forEach(option => {
                let optionKey = option.Id;
                if (OptionArray.hasOwnProperty(optionKey)) {
                  option.selected = 1;
                  OptionGroup.selected = optionKey;
                }
              });
            }
          });
        }
      }
    });
  }
