  applyCoupon() {
    this.couponVal = false;
    this.CouponArray.forEach(element => {
      if (element.CouponCode == this.order.CouponCode) {
        this.couponVal = true;
        let DiscountAmount = 0;
        if (
          this.order.BillAmount > element.MiniOrderValue ||
          element.MiniOrderValue == 0
        ) {
          if (element.DiscountType == 2) {
            DiscountAmount = element.DiscountValue;
          } else {
            DiscountAmount =
              (this.order.BillAmount / 100) * element.DiscountValue;

            if (
              DiscountAmount > element.MaxDiscountAmount &&
              element.MaxDiscountAmount != 0
            ) {
              DiscountAmount = element.MaxDiscountAmount;
            }
          }

          this.order.DiscAmount = DiscountAmount;
          this.order.Discount = 0;
        }
      }
    });
    if (this.order.OrderTypeId == 1) {
      this.LOCS.setData(this.currentTableKey, this.order);
    }
  }

