  createOrder(type, item, issocket) {
    this.clearOrder();
    if (type == 1) {
      this.currentTable = item.TableName;
      this.orderName = item.TableName;
      item.TableStatusId = 1;
      this.order.DiningTableId = item.Id;
      let splitId = item.lastspliteid == undefined ? 0 : item.lastspliteid;
      this.order.SplitTableId = item.lastspliteid;
      this.currentTableKey = "TableOrder_" + item.Id + "_" + splitId;
      var TableOrder = this.LOCS.getDataall(this.currentTableKey);
      if (TableOrder == null) {
        this.LOCS.setData(this.currentTableKey, this.order);

        this.order.OrderTypeId = type;
      } else {
        for (var k in TableOrder) this.order[k] = TableOrder[k];
      }
      if (item.lastspliteid > 0) {
        this.currentSpliteItem = item;
      } else {
        this.currentSpliteItem = null;
      }
      this.getTables();
    } else {
      this.order.OrderTypeId = type;
      if (type == 2) {
        this.order.DiningTableId = null;
        this.orderName = "TakeAway";
      }
      else if (type == 3) {
        this.order.DiningTableId = null;
        this.orderName = "Pickup/Delivery";
        this.order.CustomerDetails.val = 1;
        this.order.IsAdvanceOrder = true;
      } else if (type == 5) {
        this.order.DiningTableId = null;
        this.orderName = "QuickOrder";
      }
    }
    if (this.currentSpliteItem != null) {
      this.orderName = this.currentTable + '/' + this.currentTableKey.slice(13, 14);
    }
    if (issocket != 1) {
      let element = document.getElementById("placeorder") as HTMLElement;
      element.click();
    }
    this.updatedNotification();
  }
