  SpliteTable(table) {
    var parentID = table.Id.toString();
    var spliteTable = this.LOCS.getDataall("spliteTable");
    if (spliteTable == null) {
      spliteTable = {};
    }
    if (spliteTable[parentID] == null) {
      spliteTable[parentID] = [];
    }
    spliteTable[parentID].push(table);
    var lastindex = spliteTable[parentID].length;
    var lastelement = spliteTable[parentID];
    if (lastelement[lastindex - 2] != null) {
      var lstele = lastelement[lastindex - 2];
      table.lastspliteid = lstele.lastspliteid + 1;
    } else {
      table.lastspliteid = 2;
    }
    table.TableStatusId = 3;
    this.LOCS.setData("spliteTable", spliteTable);
    this.getTables();
    toast("newtable has been created in the name '" + table.TableName + " " + table.lastspliteid + "'")
  }
  // ngAfterContentChecked() {
