  RemoveSpliteTable(item) {
    if (item != null && item != undefined) {
      var spliteTable = this.LOCS.getDataall("spliteTable");
      var spliteID = item.Id.toString();
      var parentArray = spliteTable[spliteID];
      for (var i = 0; i < parentArray.length; i++) {
        if (parentArray[i].lastspliteid == item.lastspliteid) {
          parentArray.splice(i, 1);
        }
      }
      spliteTable[spliteID] = parentArray;
      this.LOCS.setData("spliteTable", spliteTable);
      this.getTables();
      event.stopPropagation();
      this.LOCS.RemoveKey(this.currentTableKey);
    }
  }
