  setPrice() {
    this.order.PaidAmount = 0;
    let count = 0;
    let Id;
    this.paymentType.forEach(element => {
      if (element.Price > 0) {
        count++;
        Id = element.Id;
        this.order.splitPay({ Amount: element.Price, PaymentId: element.Id });
      }
      this.order.PaidAmount = this.order.PaidAmount + element.Price;
    });
    if (count == 1) {
      this.order.PaymentTypeId = Id;
    }
  }
