  saveOrder() {
    if (this.order.validate()) {
      this.ShowCharge = false;
      this.order.BillDateTime = moment().format("YYYY-MM-DD HH:mm");
      this.order.BillDate = moment().format("YYYY-MM-DD");
      this.order.OrderedDateTime = moment().format("YYYY-MM-DD HH:mm:ss");
      this.order.OrderedDate = moment().format("YYYY-MM-DD");
      if (this.order.DeliveryDate != undefined) {
        this.order.DeliveryDateTime = moment(this.order.DeliveryDate).format('YYYY-MM-DD') + ' ' + this.order.DeliveryTime;
      }
      // console.log(this.order);
      if (this.order.OrderTypeId == 1 || this.order.OrderTypeId == 5) {
        this.order.OrderStatusId = 5;
      }
      else {
        this.order.OrderStatusId = 2;
      }
      this.order.islive = 0;
      this.order.issync = 0;
      if (this.order.OrderNo == null) {
        this.order.OrderNo = +localStorage.getItem("OrderNo")
        this.order.Id = +localStorage.getItem("OrderNo");
        localStorage.setItem("OrderNo", (this.order.OrderNo + 1).toString());
      }
      if (this.order.OrderTypeId != 1 && this.order.OrderTypeId != 5) {
        var preorders = JSON.parse(localStorage.getItem("preOrders"));
        preorders.push(this.order);
        localStorage.setItem("preOrders", JSON.stringify(preorders));
      } else {
        var pendingReceipts = JSON.parse(localStorage.getItem("pendingReceipts"));
        pendingReceipts[this.order.Id] = this.order;
        localStorage.setItem("pendingReceipts", JSON.stringify(pendingReceipts));
      }
      // if (this.socket != undefined)
      //   this.socket.emit("neworder", this.order)
      this.ds.sync();
      // this.IDB.IDBUpdateStore("Orders", this.order);
      this.getOrderFromIDB();
      let element;
      if (this.order.OrderTypeId == 1) {
        this.LOCS.RemoveKey(this.currentTableKey);
        element = document.getElementById("dineintab") as HTMLElement;
        element.click();
      }
      if (this.order.OrderTypeId == 2) {
        element = document.getElementById("takeawaytab") as HTMLElement;
        element.click();
      } else if (this.order.OrderTypeId == 3) {
        element = document.getElementById("deliverytab") as HTMLElement;
      }
      if (this.order.OrderTypeId == 5) {
        this.clearOrder();
        this.createOrder(5, {}, 0);
      } else {
        this.clearOrder();
        element.click();
      }
      this.RemoveSpliteTable(this.currentSpliteItem);
      // this.clearOrder();
      this.getPaymentTypes();
      this.getCustomer();
      this.updatedNotification();
    }
  }
