  formatter = (x: { Product: string }) => x.Product;

  customfilter(items, term) {
    var splitedterm: string[] = term.split(/\s/g);
    var /** @type {?} */ toCompare = term.toLowerCase();
    /**
     * @param {?} item
     * @param {?} term
     * @return {?}
     */
    function checkInside(item, term) {
      for (var /** @type {?} */ property in item) {
        if (item[property] === null || item[property] == undefined) {
          continue;
        }
        if (typeof item[property] === 'object') {
          if (checkInside(item[property], term)) {
            return true;
          }
        }
        // console.log(item[property])
        if (splitedterm.length == 1 && item[property].toString().toLowerCase().includes(toCompare)) {
          // console.log(item[property])
          return true;
        }
        if (splitedterm.length > 1) {
          var spliteddata: string[] = item[property].toString().toLowerCase().split(/\s/g);
          var bool = true;
          if (spliteddata.length >= splitedterm.length) {
            for (let i = 0; i < splitedterm.length; i++) {
              bool = bool && (spliteddata[i].includes(splitedterm[i]));
            }
            if (bool) {
              return true;
            }
          }
        }
      }
      return false;
    }
    return items.filter(function (item) {
      return checkInside(item, term);
    });
  };
