  OrderStatusChange(order, uporderid, statusid) {
    var statusdata = {
      new_status: "",
      message: "Order Accepted from restaurant"
    }
    this.ds.stopnotification_req();
    if (statusid == 1) {
      statusdata.new_status = "Acknowledged";
    } else if (statusid == 3) {
      statusdata.new_status = "Food Ready";
    } else if (statusid == 4) {
      statusdata.new_status = "Dispatched";
    } else if (statusid == 5) {
      statusdata.new_status = "Completed"
    } else if (statusid == -1) {
      this.ds.stopnotification_req();
      statusdata.new_status = "Cancelled"
      statusdata.message = this.cancellationmessage
    }
    // order.orderStatusId = statusid;
    order.loading = true;
    this.Auth.UPOrderStatusChange(uporderid, JSON.stringify(statusdata), statusid).subscribe(data => {
      var response: any = data;
      if (response.status == 200) {
        order.orderStatusId = statusid;
      }
      order.loading = false;
      this.signalR.getOrders();
    });
  }

