  cancelonlineorder(order, statusid) {
    this.ds.stopnotification_req();
    var statusdata = {
      "new_status": "Cancelled",
      "message": this.cancellationmessage
    }
    // var statusdata = "{\n\t\"new_status\": \"Cancelled\",\n\t\"message\": \"Order Accepted from restaurant\"\n}\n"
    // this.ordersocket.emit('editorder', order.StoreId + '/' + order.UPOrderId);
    this.Auth.UPOrderStatusChange(order.UPOrderId, JSON.stringify(statusdata), statusid).subscribe(data => {
      console.log(typeof (data));
      var response: any = data;
      if (typeof (data) == 'string') {
        response = JSON.parse(data);
      }
      console.log(response);
      if (response.status == 'error') {
        document.getElementById('errors').getElementsByClassName('modal-body')[0].innerHTML = response.message;
        console.log(document.getElementById('errors'));
        var button = document.createElement('button');
        button.setAttribute('data-toggle', 'modal');
        button.setAttribute('data-target', '#errors');
        button.hidden = true;
        document.body.appendChild(button);
        button.click();
      } else if (response.status == 'success') {
        this.ordersocket.emit('status_change', order.StoreId + '/' + order.UPOrderId, statusid);
        // this.ordersocket.emit('getorders');
      }
    });
  }
