  getElapsedTime(entry: Entry): TimeSpan {
    let totalSeconds = Math.floor((new Date().getTime() - new Date(entry.acceptedstamp).getTime()) / 1000);
    // let milliseconds = Math.floor((new Date().getTime() - new Date(entry.acceptedstamp).getTime()));
    let hours = 0;
    let minutes = 0;
    let seconds = 0;

    if (totalSeconds >= 3600) {
      hours = Math.floor(totalSeconds / 3600);
      totalSeconds -= 3600 * hours;
    }

    if (totalSeconds >= 60) {
      minutes = Math.floor(totalSeconds / 60);
      totalSeconds -= 60 * minutes;
    }
    seconds = totalSeconds;
    var riderstatus = '';
    if (entry.RiderDetails)
      riderstatus = entry.RiderDetails.delivery_info.current_state.replace(/\s/g, "").toLowerCase();

    if (entry.alertbool && new Date(entry.acceptedstamp) > new Date(0)) {
      if ((riderstatus == 'atstore')) {
        this.foodreadyalert('start');
        console.log(entry.items);
        entry.alertbool = false;
      }
      if ((riderstatus == 'assigned') && minutes > 10) {
        this.foodreadyalert('start');
        console.log(entry.items);
        entry.alertbool = false;
      }
      if ((riderstatus == '') && minutes > 15) {
        this.foodreadyalert('start');
        console.log(entry.items);
        entry.alertbool = false;
      }
    }

    return {
      hours: hours,
      minutes: minutes,
      seconds: seconds,
      millisecond: 0
    };
  }
  foodreadyalert(action) {
    return;
    if (this.electron.isElectronApp && action == 'start') {
      this.sound.play();
    }
    if (this.electron.isElectronApp && action == 'stop') {
      this.sound.stop();
    }
  }
  handleAddressChange(address: Address) {
    console.log(address);
    this.order.CustomerDetails.Address = '';
    var address_limit_index = address.address_components.findIndex(x => x.types.some(y => y === 'locality'));
    address.address_components.forEach((element, index) => {
      if (index < address_limit_index) {
        this.order.CustomerDetails.Address = this.order.CustomerDetails.Address + element.long_name + (index == address_limit_index - 1 ? '' : ',');
      }
    });
    this.order.CustomerDetails.City = address.address_components.filter(x => x.types.some(y => y === 'locality'))[0].long_name;
    this.order.CustomerDetails.PostalCode = +address.address_components.filter(x => x.types.some(y => y === 'postal_code'))[0].long_name;
    this.order.CustomerDetails.googlemapurl = address.url;
    console.log(this.order.CustomerDetails.Address.includes(address.name));
  }

