import { Component, OnInit, Injectable } from "@angular/core";
import { Router, NavigationStart } from "node_modules/@angular/router";
import { AuthService } from "../auth.service";
import { Password } from "./password";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import { Observable, fromEvent, merge, of } from 'rxjs';
import { mapTo } from 'rxjs/operators';
import { DataService } from '../service/data.service';
// import { SocketService } from '../socket.service';
import * as moment from "moment";
import { element } from 'protractor';
import { OrderComponent } from '../order/order.component';
import { dangertoast, toast } from 'src/assets/dist/js/toast-data';
import { Socket } from 'ngx-socket-io';
import { Howl, Howler } from 'howler';
import { ElectronService } from 'ngx-electron';
import { HttpClient } from '@angular/common/http';
import * as io from 'socket.io-client';
import { SignalRService } from '../service/signal-r.service';
// import { dirname } from 'path';
@Injectable({
  providedIn: 'root'
})
@Component({
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.css"]
})
export class HeaderComponent implements OnInit {
  showHead: boolean = false;
  AppName = "Attendance";
  PassWordObject = new Password(localStorage.getItem("userid"), "", "", "");
  closeResult: string;
  userid: any;
  pass_alert: String;
  passAlertVisible = false;
  Show_hk_nav_light = true;
  pageName = "";
  user: any;
  role: void;
  name: void;
  online$: Observable<boolean>;
  closingBalance: number;
  endShift: boolean = false;
  OnlineOrders: Observable<string[]>;
  orders;
  scktOrders = 0;
  logInfo: any;
  CompanyId: any;
  StoreId: any;
  loading: boolean = false;
  errocount: any;
  // sound = new Howl({
  //   src: [__dirname+'/assets/audio/plucky.mp3'],
  //   loop: true
  // });
  waiter_socket: SocketIOClient.Socket;
  sound;
  private socketServer = (this.electron.isElectronApp) ? this.electron.remote.require('./socket-server/src/socket-server') : null;
  constructor(
    public router: Router,
    private Auth: AuthService,
    private ds: DataService,
    private order: OrderComponent,
    private http: HttpClient,
    private modalService: NgbModal,
    private socket: Socket,
    private electron: ElectronService,
    private signalR: SignalRService

  ) {
    var logInfo = JSON.parse(localStorage.getItem("logInfo"));
    this.CompanyId = logInfo.CompanyId;
    this.StoreId = logInfo.StoreId;
    this.online$ = merge(
      of(navigator.onLine),
      fromEvent(window, 'online').pipe(mapTo(true)),
      fromEvent(window, 'offline').pipe(mapTo(false))
    );
    this.ds = ds;
    if (this.electron.isElectronApp) {
      this.sound = new Howl({
        src: [__dirname + '/assets/audio/plucky.mp3'],
        loop: true
      });
    }
    // on route change to '/login', set the variable showHead to false
    // console.log(router.url)
    router.events.forEach(event => {
      // console.log(event)
      if (event instanceof NavigationStart) {
        // console.log(event["url"]);
        this.pageName = event["url"].replace("/", "");
        // console.log(this.pageName);

        if (event["url"] == "/") {
          this.showHead = false;
        } else if (event["url"] == "/signup") {
          this.showHead = false;
          this.AppName = "";
        } else if (event["url"] == "/unlockscreen") {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
  }

  ngOnInit() {
    // this.socket.on('neworder', data => {
    //   console.log('toast(data)')
    //   // console.log(toast(data))
    // });
    // this.socket.on('orders', data => {
    //   // console.log('orders socket event')
    //   // console.log(data)
    //   var orders: any = data;
    //   if (this.electron.isElectronApp) {
    //     this.sound.stop();
    //     if (orders.some(x => x.OrderStatusId == 0)) {
    //       this.sound.play();
    //     }
    //   }
    // });
    if (!navigator.onLine) {
      document.getElementById("navbar_search_btn").click();
    }
    this.orders = 0;
    this.user = JSON.parse(localStorage.getItem("user"));
    this.logInfo = JSON.parse(localStorage.getItem("logInfo"));
    this.userid = localStorage.getItem("userid");
    this.online$.subscribe(data => {
      if (data) {
        document.getElementById("navbar_search_close").click();
        this.ds.sync();
      } else {
        console.log("offline")
        document.getElementById("navbar_search_btn").click();
      }
    });
    for (var key in localStorage) {
      if (!key.search(/TableOrder/gi)) {
        this.orders = this.orders + 1;
      }
    }
    this.ds.getupdated().subscribe(data => {
      this.orders = data;
    });
    this.loadScripts();
    this.waitersocket();
  }
  signalRconfig() {
    this.ds.stopnotification().subscribe(data => {
      this.sound.stop();
    })
    this.signalR.hubconnection.on("NewOrder", data => {
      if (this.electron.isElectronApp) {
        this.sound.stop();
        this.sound.play();
        console.log(toast(data))
      }
    });
    this.signalR.hubconnection.on("order", orders => {
      if (this.electron.isElectronApp && orders.some(x => x.OrderStatusId == 0)) {
        this.sound.stop();
        this.sound.play();
      }
    });
    this.signalR.hubconnection.on("joinmessage", data => {
      console.log(data);
    });
  }

  waitersocket() {
    if (this.electron.isElectronApp) {
      if (localStorage.getItem('server-ip-address') != ('' || null || undefined)) {
        var ip = localStorage.getItem('server-ip-address');
        this.socketServer.startServer(ip);
        // this.waiter_socket = io("http://" + ip + ":8000/");
      }
    }
    if (localStorage.getItem('server-ip-address') != ('' || null || undefined)) {
      var ip = localStorage.getItem('server-ip-address');
      // this.socketServer.startServer(ip);
      this.waiter_socket = io("http://" + ip + ":8000/");
    }

    // if (this.electron.isElectronApp) {
    //   var ip = this.electron.remote.getGlobal("getip")();
    //   this.waiter_socket = io("http://" + ip + ":8000/");
    //   console.log(ip);
    // }
    // console.log(this.waiter_socket);
    // this.waiter_socket = io();
    if (this.waiter_socket != undefined) {
      this.waiter_socket.on('connect', data => {
        this.waiter_socket.emit('join', 'EXE');
      });
      this.waiter_socket.on("getdiningdata", (waiter) => {
        var diningdata = {
          diningareas: JSON.parse(localStorage.getItem('DiningArea')),
          diningtables: JSON.parse(localStorage.getItem('DiningTable'))
        }
        this.waiter_socket.emit("diningdata", diningdata, waiter)
      });
      this.waiter_socket.on("getproducts", (waiter) => {
        var products = {
          products: localStorage.getItem('Product'),
          categories: localStorage.getItem('Category')
        }
        this.waiter_socket.emit("products", products, waiter);
      });
      this.waiter_socket.on("getTableData", (waiter, tableid) => {
        var tabledata = null
        for (var key in localStorage) {
          if (!key.search(/TableOrder/gi)) {
            var splitkey = key.split('_');
            if (splitkey[1] == tableid) {
              tabledata = localStorage.getItem(key);
            }
          }
        }
        this.waiter_socket.emit("tableData", tabledata, waiter);
      });
    }
  }

  loadScripts() {
    const dynamicScripts = [
      "./assets/dist/js/init.js",
      "./assets/vendors/jquery/dist/jquery.min.js",
      "./assets/vendors/popper.js/dist/umd/popper.min.js",
      "./assets/vendors/bootstrap/dist/js/bootstrap.min.js",
      "./assets/dist/js/jquery.slimscroll.js",
      "./assets/dist/js/dropdown-bootstrap-extended.js",
      "./assets/dist/js/feather.min.js",
      "./assets/vendors/jquery-toggles/toggles.min.js",
      "./assets/dist/js/toggle-data.js",
      "./assets/vendors/waypoints/lib/jquery.waypoints.min.js",
      "./assets/vendors/jquery.counterup/jquery.counterup.min.js",
      "./assets/vendors/raphael/raphael.min.js",
      "./assets/vendors/morris.js/morris.min.js",
      "./assets/vendors/echarts/dist/echarts-en.min.js",
      // "./assets/vendors/jquery.sparkline/dist/jquery.sparkline.min.js",
      "./assets/vendors/vectormap/jquery-jvectormap-2.0.3.min.js",
      "./assets/vendors/vectormap/jquery-jvectormap-world-mill-en.js",
      "./assets/dist/js/vectormap-data.js",
      "./assets/vendors/owl.carousel/dist/owl.carousel.min.js",
      "./assets/vendors/jquery-toast-plugin/dist/jquery.toast.min.js",
      "./assets/vendors/select2/dist/js/select2.full.min.js",
      "./assets/dist/js/select2-data.js",
      "./assets/dist/js/login-data.js"
    ];
    for (let i = 0; i < dynamicScripts.length; i++) {
      const node = document.createElement("script");
      node.src = dynamicScripts[i];
      node.type = "text/javascript";
      node.async = false;
      node.charset = "utf-8";
      document.getElementsByTagName("head")[0].appendChild(node);
    }
  }
  getstoredata() {
    this.loading = true;
    this.Auth.getStoreData(this.CompanyId, this.StoreId).subscribe(data => {
      // console.log(data);
      var keys = Object.keys(data);
      // console.log(keys);
      keys.forEach((element, index, array) => {
        localStorage.setItem(element, (data[element] == null) ? JSON.stringify([]) : JSON.stringify(data[element]));
      });
      var tables = JSON.parse(localStorage.getItem('DiningTable'));
      tables.forEach(table => {
        table.isordering = false;
      });
      localStorage.setItem('DiningTable', JSON.stringify(tables));
      this.loading = false;
      console.log(toast('Latest data refreshed successfully'));
      this.router.navigate(["/unlockscreen"]);
    });
  }
  showLeftNav() {
    if (this.pageName == "order") {
      return false;
    } else {
      return true;
    }
  }
  errors() {
    var pendingreceipts = JSON.parse(localStorage.getItem("pendingReceipts"));
    this.errocount = Object.values(pendingreceipts).length;
    var message = (this.errocount > 0) ? this.errocount + " Receipt(s) pending! " : ""
    return message;
  }
  open(content, $event) {
    $event.preventDefault();
    this.PassWordObject = new Password(
      localStorage.getItem("userid"),
      "",
      "",
      ""
    );
    this.modalService
      .open(content, { ariaLabelledBy: "modal-basic-title", centered: true })
      .result.then(
        result => {
          this.closeResult = `Closed with: ${result}`;
        },
        reason => {
          this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        }
      );
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return "by pressing ESC";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "by clicking on a backdrop";
    } else {
      return `with: ${reason}`;
    }
  }
  showAlertmsg(data) {
    this.pass_alert = data.msg;
    this.passAlertVisible = true;
  }
  popup(contentDetail) {
    const modalRef = this.modalService
      .open(contentDetail, {
        ariaLabelledBy: "modal-basic-title",
        centered: true
      })
      .result.then(
        result => {
          //this.closeResult = `Closed with: ${result}`;
        },
        reason => {
          //this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        }
      );
    //var userid = this.userid;
  }
  logout() {
    var localstoragenames = []
    for (let i = 0; i < localStorage.length; i++) {
      localstoragenames.push(localStorage.key(i))
    }
    localstoragenames.forEach(element => {
      localStorage.removeItem(element)
    })
    this.socket.disconnect();
    this.signalR.disconnect();
    this.router.navigate(["/"]);
    // localStorage.removeItem("logInfo");
    // localStorage.removeItem("storeUsers");
    // localStorage.removeItem("user");
    // localStorage.removeItem("shift");
    // localStorage.removeItem("OrderNo");
    // localStorage.removeItem("KOTNo");
    // localStorage.removeItem("spliteTable");
    indexedDB.deleteDatabase("biz1book")
  }
  lockAcc() {
    this.router.navigate(["/unlockscreen"]);
  }
  closendlock() {
    var shift = JSON.parse(localStorage.getItem("shift"));
    this.Auth.EditShift(shift.Id, this.closingBalance, moment().format("YYYY-MM-DD HH:mm:ss")).subscribe(data => {
      localStorage.removeItem("shift");
      localStorage.removeItem("user");
      this.router.navigate(["/unlockscreen"]);
    });
  }
}
