import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';

@Pipe({
  name: 'moment'
})
export class MomentPipe implements PipeTransform {

  transform(date: any, format: any, time: any): any {
    // console.log(time,moment(time, ["HH:mm"]).format('hh:mm a'));
    var formateddate;
    if(date != undefined) {
      formateddate = moment(date).format(format)+' '+moment(time, ["HH:mm"]).format('hh:mm A');
    } else {
      formateddate = '-'
    }
    return formateddate;
  }

}
