  GenerateKey(obj): string {
    let Itemkey = obj.Id;
    //console.log(obj);
    //let VariantGroup = obj.VariantGroup;
    //let AddonGroup = obj.AddonGroup;
    let VariantKay = "";

    if (obj.hasOwnProperty("VariantGroup")) {
      obj.VariantGroup.forEach(VariantGroup => {
        if (VariantGroup.hasOwnProperty("varient")) {
          VariantGroup.varient.forEach(element => {
            if (element.selected == 1) {
              //AddonKay = AddonKay + "-" + element.Id;
              VariantKay = VariantKay + "-" + element.Id;
            }
          });
        }

        // if (element.selected > 0) {
        //   VariantKay = VariantKay + "-" + element.selected;
        // }
      });
    }

    VariantKay = VariantKay[0] == "-" ? VariantKay.substring(1) : VariantKay;
    let AddonKay = "";
    if (obj.hasOwnProperty("AddonGroup")) {
      obj.AddonGroup.forEach(AddGroup => {
        if (AddGroup.hasOwnProperty("Addon")) {
          AddGroup.Addon.forEach(element => {
            if (element.selected == 1) {
              AddonKay = AddonKay + "-" + element.Id;
            }
          });
        }
      });
    }

    AddonKay = AddonKay[0] == "-" ? AddonKay.substring(1) : AddonKay;

    let OptionKey = "";
    if (obj.hasOwnProperty("OptionGroup")) {
      obj.OptionGroup.forEach(OptionGroup => {
        if (OptionGroup.hasOwnProperty("Option")) {
          OptionGroup.Option.forEach(element => {
            if (element.selected == 1) {
              OptionKey = OptionKey + "-" + element.Id;
            }
          });
        }
      });
    }

    OptionKey = OptionKey[0] == "-" ? OptionKey.substring(1) : OptionKey;

    Itemkey = Itemkey + "_" + VariantKay + "_" + AddonKay + "_" + OptionKey;
    return Itemkey;
  }
