  mergeDuplicateItem(obj) {
    var removeFlag = 0;
    this.item.forEach(function (value) {
      if (
        value.Key == obj.Key &&
        value.operationFlag == 0 &&
        obj.operationFlag == 1
      ) {
        let currentQty = obj.Quantity;
        value.Quantity = value.Quantity + currentQty;
        removeFlag = 1;
      }
    });

    if (removeFlag) {
      var index = this.item.findIndex(x => x.operationFlag == 1);
      this.item.splice(index, 1);
    }
  }
