  AddAdditionalCharges(obj): void {
    this.AdditionalCharge = [];
    this.setTaxes();
    this.setBillAmount(null);
    obj.forEach(element => {
      if (element.selected) {
        this.AdditionalCharge.push(new AdditionalChargeModule(element));
      }
    });
    this.setTaxes();
    this.setBillAmount(null);
  }

  updateItem(obj): any {
    let returnObj: OrderItemModule;
    obj.Key = this.GenerateKey(obj);
    let checkItem = this.CheckItem(obj);
    if (checkItem) {
      this.item.forEach(function (value) {
        if (value.Key == obj.Key) {
          obj.hasOwnProperty("VariantGroup");
          {
            value.VariantGroup = [];
            obj.VariantGroup.forEach(VariantGroup => {
              if (VariantGroup.selected > 0) {
                value.VariantGroup.push(new VariantGroupModule(VariantGroup));
              }
            });
          }
          obj.hasOwnProperty("AddonGroup");
          {
            value.AddonGroup = [];
            obj.AddonGroup.forEach(AddonGroup => {
              if (AddonGroup.selected > 0) {
                value.AddonGroup.push(new AddonGroupModule(AddonGroup));
              }
            });
          }
        }
      });
    }
    this.setTaxes();
    this.setBillAmount(null);
    //return returnObj;
  }
  splitPay(obj) {
    this.splitPayment.push(new SplitPaymentModule(obj));
  }
  convertonlineorder(order, compid, storeid) {
    var onlineorder = {
      Id: 0,
      OrderId: 0,
      OrderTypeId: 4,
      OrderedDate: moment(order.order.details.created).format("YYYY-MM-DD hh:mm A"),
      OrderedDateTime: moment(order.order.details.created).format("YYYY-MM-DD hh:mm A"),
      DeliveryDate: moment(order.order.details.delivery_datetime),
      DeliveryTime: moment(order.order.details.delivery_datetime).format("hh:mm A"),
      DeliveryDateTime: moment(order.order.details.delivery_datetime).format("YYYY-MM-DD hh:mm A"),
      OrderStatusId: 0,
      DiscType: 1,
      DiscPercent: 0,
      DiscAmount: order.order.details.discount,
      Charges: order.order.details.charges,
      CouponCode: order.order.details.coupon,
      CouponDiscAmount: 0,
      Tax1: 0,
      Tax2: 0,
      Tax3: 0,
      StoreId: order.order.store.merchant_ref_id,
      CustomerId: 0,
      CustomerNo: 1,
      CustomerDetails: {
        Id: 0,
        Name: order.customer.name,
        Email: order.customer.email,
        PhoneNo: order.customer.phone,
        Address: order.customer.address.line_1 + order.customer.address.line_2,
        City: order.customer.address.city,
        PostalCode: order.customer.address.pin,
        CompanyId: compid,
        StoreId: storeid
      },
      CompanyId: compid,
      BillAmount: order.order.details.order_total,
      PaidAmount: order.order.details.order_total,
      BillDateTime: moment(order.order.details.created).format("YYYY-MM-DD hh:mm A"),
      BillDate: moment(order.order.details.created).format("YYYY-MM-DD hh:mm A"),
      BillStatusId: 5,
      DiningTableId: null,
      SplitTableId: null,
      WaiterId: null,
      CashierId: JSON.parse(localStorage.getItem("user")).Id,
      Note: order.order.details.instructions,
      items: [],
      AdditionalCharge: [],
      KOT: [],
      issync: 0,
      islive: 0,
      InvoiceNo: storeid + moment().format("YYYYMMDD") + "/" + localStorage.getItem("OrderNo"),
      PaymentTypeId: 4,
      OrderNo: 0,
      UserId: JSON.parse(localStorage.getItem("user")).Id,
      IsAdvanceOrder: 1,
      Source: order.order.details.channel,
      SourceId: 0,
      AggregatorOrderId: order.order.details.ext_platforms.id,
      UPOrderId: order.order.details.id
    }
    order.order.items.forEach(itm => {
      var item = {
        Id: 0,
        ProductId: +itm.merchant_id,
        Product: itm.title,
        Quantity: itm.quantity,
        Price: itm.price,
        StatusId: 1,
        KOTId: 0,
        DiscType: 1,
        DiscPercent: 0,
        DiscAmount: itm.discount,
        TotalPrice: itm.total,
        Options: [],
        ComplementryQty: 0,
        Tax1: 0,
        Tax2: 0,
        Tax3: 0,
        Note: '',
      }
      ///OPTION
      itm.options_to_add.forEach(option => {
        var opt = {
          OptionId: option.merchant_id,
          Price: option.price,
          Name: option.title,
        }
        item.Options.push(opt);
      });
      ///TAXES
      itm.taxes.forEach(tax => {
        if (tax.title == "CGST") {
          item.Tax1 = tax.value;
        } else if (tax.title == "SGST") {
          item.Tax2 = tax.value;
        }
      });
      onlineorder.items.push(item);
    });
    return onlineorder;
  }
