import { Component, OnInit, ViewChild, ɵConsole } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthService } from "../auth.service";
import { Router } from "@angular/router";
import { LocsService } from '../service/locs.service';
import * as moment from "moment";

@Component({
  selector: "app-signup",
  templateUrl: "./signup.component.html",
  styleUrls: ["./signup.component.css"]
})
export class SignupComponent implements OnInit {
  constructor(
    private formBuilder: FormBuilder,
    private Auth: AuthService,
    private router: Router,
    private LOCS: LocsService
  ) {}

  registerForm: FormGroup;
  submitted = false;
  alert_msg = "";

  ngOnInit() {
    showHead: false;

    this.registerForm = this.formBuilder.group({
      firstName: ["", Validators.required],
      lastName: "",
      CompanyName: ["", Validators.required],
      DisplayName: "",
      email: [""],
      password: ["", Validators.required],
      confirmPassword: ["", Validators.required],
      pnoneNo: ""
    });

    this.registerForm.valueChanges.subscribe(term => {
      this.alert_msg = "";
    });
  }

  get f() {
    return this.registerForm.controls;
  }
  onSubmitHandler(data) {
    console.log(data);
    var status = data.status;
    var msg = data.msg;
    if (status == 200) {
      this.router.navigate([""]);
    } else {
      this.alert_msg = msg;
    }
  }

  onSubmit() {
    this.submitted = true;
    if (this.registerForm.invalid) {
      return;
    }
    if (
      this.registerForm.value.password !=
      this.registerForm.value.confirmPassword
    ) {
      this.alert_msg = "Password and confirm Password is not match";
      return;
    }
    console.log(this.registerForm.value);
    this.Auth.Registration(this.registerForm.value).subscribe(data => {
      this.onSubmitHandler(data);
    });
  }
}
