import { Component, HostListener } from "@angular/core";
import { Router, NavigationStart } from "@angular/router";
import { DataService } from './service/data.service';
import { idbService } from './service/idb.service';
import { SignalRService } from './service/signal-r.service';
// import { DataService } from './service/data.service';
//declare function setHeightWidth(): any;
import { NgxIndexedDBService } from 'ngx-indexed-db';
// import { Socket } from 'ngx-socket-io';
import { GlobalConstants } from './common/global-constants'
import { OrderComponent } from './order/order.component'
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Howl, Howler } from 'howler';
import { ElectronService } from 'ngx-electron';
import { dangertoast, toast } from 'src/assets/dist/js/toast-data';

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent {
  // @HostListener('window:beforeunload', ['$event'])
  // beforeunloadHandler(event) {
  //   localStorage.setItem("lockStatus", "locked");
  // }
  title = "Biz1book";
  pageName = "";
  //type = "page";
  showHead: boolean = true;
  orderhub = GlobalConstants.hubconnection;
  order: OrderComponent;
  CompanyId;
  StoreId;
  hubRoom;
  sound;

  constructor(public router: Router, private datService: DataService, private idb: idbService,
    private signalR: SignalRService,
    private electron: ElectronService,
    private ds: DataService
  ) {
    var logInfo = JSON.parse(localStorage.getItem("logInfo"));
    // this.CompanyId = logInfo.CompanyId;
    // this.StoreId = logInfo.StoreId;
    // this.hubRoom = this.StoreId + '/' + this.CompanyId;
    this.ds = ds;
    if (this.electron.isElectronApp) {
      this.sound = new Howl({
        src: [__dirname + '/assets/audio/plucky.mp3'],
        loop: true
      });
    }

    // router.events.forEach(event => {
    router.events.forEach(event => {
      //console.log(event)
      if (event instanceof NavigationStart) {
        console.log(event["url"]);
        this.pageName = event["url"].replace("/", "");
        if (event["url"] == "/") {
          this.showHead = false;
        } else if (event["url"] == "/signup") {
          this.showHead = false;
          //this.AppName = "";
        } else if (event["url"] == "/unlockscreen") {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
    router.events.forEach(event => {
      if (event instanceof NavigationStart && (event["url"] != "/") && (event["url"] != "/signup") && (event["url"] != "/unlockscreen")) {
        // console.log(event["url"]);
        this.datService.execute();
      }
    });

    // this.socket.emit("join","test")
    // this.socket.on("getLocData", () => {
    //   console.log("localdata");
    //   this.socket.emit("locData",localStorage.getItem("logInfo"))
    // })
    // this.orderhub.onclose((error) => {
    //   GlobalConstants.ishubconnected = false;
    //   this.signalRconnect()
    // })
    // this.signalR.hubconnection.onclose(()=>{
    //   console.log("disconnected");
    //   this.signalR.connect();
    // })
    this.ds.stopnotification().subscribe(data => {
      this.sound.stop();
    })
    this.signalR.hubconnection.on("NewOrder", data => {
      console.log(toast(data))
      if (this.electron.isElectronApp) {
        this.sound.stop();
        this.sound.play();
      }
    });
    this.signalR.hubconnection.on("order", orders => {
      if (this.electron.isElectronApp && orders.some(x => x.OrderStatusId == 0)) {
        this.sound.stop();
        this.sound.play();
      }
    });
    this.signalR.hubconnection.on("joinmessage", data => {
      console.log(data);
    });
    this.signalR.connect();
  }



  sleep(ms) {
    // ret urn;
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  FixedLeftNav() {
    if (this.pageName == "order") {
      return true;
    } else {
      return false;
    }
  }
}

